import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable()
export class StaffService {

  constructor(private http: HttpClient) { }

  addStaff(payload) {
    return this.http.post(environment.url + `api/Staffs`, payload);
  }

  uploadProfileImage(staffId, payload) {
    return this.http.post(environment.url + `api/Staffs/${staffId}/ProfileImage`, payload);
  }

  getAllStaffs() {
    return this.http.get(environment.url + `api/Staffs`);
  }

  getStaffById(staffId) {
    return this.http.get(environment.url + `api/Staffs/${staffId}`);
  }

  updateStaff(staffId, payload) {
    return this.http.put(environment.url + `api/Staffs/${staffId}`, payload);
  }

  uploadDigitalSign(staffId, payload) {
    return this.http.post(environment.url + `api/Staffs/${staffId}/DigitalSign`, payload);
  }

  getDigitalSign(staffId) {
    return this.http.get(environment.url + `api/Staffs/${staffId}/DigitalSign`);
  }

  deleteStaff(staffId) {
    return this.http.delete(environment.url + `api/Staffs/${staffId}`);
  }

}
