import { DepartmentService } from '../../settings/departments/department.service';
import { Component, OnInit } from '@angular/core';
import { StaffService } from '../staff.service';
import * as $ from 'jquery';

@Component({
  selector: 'app-staff-edit',
  templateUrl: './staff-edit.component.html',
  styleUrls: ['./staff-edit.component.scss'],
  providers: [StaffService, DepartmentService]
})
export class StaffEditComponent implements OnInit {

  staffId;
  baseUrl;
  profileImage;
  departments;
  staffDetail = [];

  staffEditForm;

  signatureImage;

  selectedDepartment = [];
  selectedDepartmentId = [];
  digitalSignature;

  constructor(){

  }

  ngOnInit() {

    $(document).ready(function () {

      // ------------------------------------------------------- //
      // Multi Level dropdowns
      // ------------------------------------------------------ //
      $(document).on("mouseover", "ul.dropdown-menu [data-toggle='dropdown']", function (event) {
        event.preventDefault();
        event.stopPropagation();
        // $(this).parent().childrens().toggleClass("show");
        $(this).siblings().addClass("show");
        $(this).parent().siblings().children().removeClass("show");


        if (!$(this).next().hasClass('show')) {
          $(this).parents('.dropdown-menu').first().find('.show').removeClass("show");
        }
        $(this).parents('li.nav-item.dropdown.show').on('hidden.bs.dropdown', function (e) {
          $('.dropdown-submenu .show').removeClass("show");
        });

      });


    });

  }

  //For Profile Picture Upload
  onProfilePictureSelected(event) {
    if (event.target.files.length > 0) {
      this.profileImage = <File>event.target.files[0];
    } else {
      alert("Photo not selected")
    }
  }


}
